footer {
  background-color: #224095;
  color: white;
  text-align: center;
  padding: 1%;
  width: 100%;
}

.footer-clg-icon {
  display: flex;
  align-items: center;
  .footer-clg-name {
    display: flex;
    flex-direction: column;
    align-items: start;

    h5 {
      font-weight: 400;
      text-align: start;
    }
    h2 {
      font-weight: 700;
      text-align: start;
    }
  }
}

.footer-first-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-clg-locations {
  display: flex;
  align-items: center;
  .footer-clg-address {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    span {
      font-size: 14px;
      text-align: start;
    }
  }

  svg {
    font-size: 30px;
    padding: 5px;
    color: darkgray;
  }
}

.footer-contact-icon {
  display: flex;
  align-items: center;
  font-size: 14px;
  svg {
    padding: 0px 5px 0px 5px;
    color: darkgray;
  }
}

.footer-clg-details {
  display: flex;
  gap: 20px;
  padding: 10px;
}

.footer-quick-links-header,
.footer-academics-header {
  font-weight: bold;
  padding: 5px 0px;
}

.footer-quick-links,
.footer-academics {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 15px;
}

.footer-quick-links-ul,
.footer-academics-ul {
  display: flex;
  flex-direction: column;
  align-items: start;
  list-style: none;
  font-size: 14px;
  a {
    text-decoration: none;
    color: #fff;
  }
}

.footer-essential {
  display: flex;
  gap: 50px;
}

.footer-academics-ul {
  list-style: none;
}

.footer-second-item {
  display: flex;
  justify-content: space-around;
}

.footer-clg-information {
  padding: 15px;
  display: flex;
  gap: 20px;
  flex-direction: column;
}

.footer-social-pages {
  display: flex;
  svg {
    height: 2rem;
    width: 2rem;
    color: darkgray;
  }
}

.footer-clg-code-btn {
  background-color: #77ab43 !important;
  color: white !important;
  padding: 10px 20px !important;
}

.footer-third-item {
  display: flex;
  justify-content: center;
  gap: 5%;
  // padding: 15px;
  padding: 15px 0px;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  span {
    font-weight: bold;
  }
  .footer-copy-right {
    display: flex;
    align-items: center;
    gap: 5px;
  }
}

@media only screen and (min-width: 320px) and (max-width: 600px) {
  .footer-essential {
    display: none !important;
  }

  .footer-third-item {
    flex-direction: column-reverse;
  }
}

.footer-clg-icon {
  @media (max-width: 768px) {
  }

  .footer-clg-name {
    @media (max-width: 768px) {
      margin-left: 0; // Removes margin on smaller screens.
    }

    h2 {
      @media only screen and (min-width: 320px) and (max-width: 768px) {
        font-size: 0.8rem !important;
      }

      @media (min-width: 768px) {
        font-size: 1rem !important; // Smaller font on mobile.
      }
    }

    h5 {
      @media only screen and (min-width: 320px) and (max-width: 768px) {
        font-size: 0.6rem !important;
      }

      @media (max-width: 768px) {
        font-size: 0.7rem !important;
      }
    }
  }

  img {
    width: 70px;
    height: 70px;

    @media (max-width: 768px) {
      width: 50px !important;
      height: 50px !important;
    }
  }
}

@media only screen and (min-width: 320px) and (max-width: 768px) {
  .footer-clg-details {
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
  .footer-clg-address {
    text-align: center;
  }
  .footer-clg-locations .footer-clg-address span, .footer-clg-contact span {
    font-size: 12px!important;
  }
}

.footer-social-pages{
  justify-content: center;
  gap: 5px;
}